import { useState } from "react";

const navBar = <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" className="nav-btn">
<path stroke-linecap="round" stroke-linejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
</svg>;

const cross = <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" className="close-btn">
<path stroke-linecap="round" stroke-linejoin="round" d="M6 18L18 6M6 6l12 12" />
</svg>;

const nav = <nav className="header-nav-mov">
<button className="header-btn-mov">Lottery</button>
<button className="header-btn-mov">Auction</button>
<button className="header-btn-mov">Learning</button>
<button className="header-btn-mov">Log in/Sign up</button>
</nav>;

function MobileNav(){
    const [content, changeContent] = useState(true);
    let contentToShow = content ? navBar : cross;
    function handleClick(){
        changeContent(!content);
    }  
    return <div className="mobile-nav-container">
    <div onClick={handleClick}              className="mobile-nav-btn">
                {contentToShow}
    </div>

    <div>
        {!content ? nav : ""}
    </div>
    
</div>        
}

export default MobileNav;