import { useEffect } from 'react';
function PythonCode({getContent}){
    const code = `
    import hashlib

    string = "hello world"
    hash_object = hashlib.sha256(string.encode())
    hex_dig = hash_object.hexdigest()
    
    print(hex_dig)`;
    useEffect(()=>{
        getContent(code);
    });
    return <div className='python-box'>
        <pre className='python-code'>
            {code}
        </pre>        
    </div>
}   
export default PythonCode;