import { useEffect } from 'react';
function CppCode({getContent}){
    const code = `
    #include <iostream>
    #include <string>
    #include <hash>
    
    int main() {
        std::string string = "hello world";
    
        std::hash<std::string> hash_fn;
        std::size_t hash = hash_fn(string);
    
        std::cout << hash << std::endl;
    
        return 0;
    }`;
    useEffect(()=>{
        getContent(code);
    })
    return <div className='cpp-box'>
        <pre className='cpp-code'>
            {code}
        </pre>        
    </div>
}

export default CppCode;