import PythonCode from "./code/pythonCode";
import CppCode from "./code/cppCode";
import JavaCode from "./code/javaCode";
import { useState } from "react";
import '../css/code.css';

function Code(){
    const [language, setlanguage] = useState(0);
    const [copytext, setcopytext] = useState("Copy code");
    let content;
    function getContent(str){
        content = str;
    }
    let current_language = <CppCode getContent={getContent}/>;
    switch (language){
        case 0:
            current_language = <CppCode getContent={getContent}/>;
            break;
        case 1:
            current_language = <PythonCode getContent={getContent}/>;
            break;
        case 2:
            current_language = <JavaCode getContent={getContent}/>;
            break;
        default:
            current_language = <CppCode getContent={getContent}/>;
    }
    
    async function handleCopyClick(){
        await navigator.clipboard.writeText(content);
        setcopytext("copied");
        setTimeout(() => {
            setcopytext("Copy code")
        }, 3000);
    }
    function handleClick(event){
        const btn = event.target.id;
        if(btn === 'cpp')
            setlanguage(0);
        else if(btn === 'python')
            setlanguage(1);
        else if(btn === 'java')
            setlanguage(2);
    }

    return <div className='codeBox'>
        <div className='code-heading'>
            <div className="lan-btn" onClick={handleClick}>
                <button id="cpp" className={`${language === 0 ? 'active-btn' : 'passive-btn'}`}>c++</button>
                <button id="python" className={`${language === 1 ? 'active-btn' : 'passive-btn'}`}>python</button>
                <button id="java" className={`${language === 2 ? 'active-btn' : 'passive-btn'}`}>java</button>
            </div>            
                <button className={`${copytext === "Copy code" ? "copy-btn" : "copied-btn"}`} onClick={handleCopyClick}>
                    <svg stroke="currentColor" fill="none" stroke-width="2" viewBox="0 0 24 24" stroke-linecap="round" stroke-linejoin="round" className="copy-icon" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M16 4h2a2 2 0 0 1 2 2v14a2 2 0 0 1-2 2H6a2 2 0 0 1-2-2V6a2 2 0 0 1 2-2h2"></path><rect x="8" y="2" width="8" height="4" rx="1" ry="1"></rect>
                    </svg>{copytext}
                </button>
        </div>
        {current_language}
    </div>

}

export default Code;