import {useState} from 'react';
import {sha256} from 'js-sha256';
function Blockhash(){
    const [data, setData] = useState("");
    const [hash, setHash] = useState(sha256(data));
    const [copy, setCopyState] = useState(false);
    const showCopyText = copy ? "copied" :"";
    const handleSubmit = (event)=>{
        event.preventDefault();
        const hash = sha256(data);
        setHash(hash);
        setData("")
    }
    const handleSvgClick = async ()=>{
        await navigator.clipboard.writeText(hash);
        setCopyState(true);
        setTimeout(()=>{
            setCopyState(false);
        }, 3000);
    }
    const handleChange = (event)=>{
        setData(event.target.value);
    }
    return (<div className='hash-field'>
        <form onSubmit={handleSubmit}>
            <div className='input'>
                <label>Input</label>
                <textarea placeholder='Enter any data' onChange={handleChange}value={data} />
            </div>
            <div className='output'>
                <div className='text'>Hash Value</div>
                <div className='output-field'>
                    <p>{hash}</p>
                    <div>
                        <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" className='copy-icon' onClick={handleSvgClick}>
                            <path stroke-linecap="round" stroke-linejoin="round" d="M9 12h3.75M9 15h3.75M9 18h3.75m3 .75H18a2.25 2.25 0 002.25-2.25V6.108c0-1.135-.845-2.098-1.976-2.192a48.424 48.424 0 00-1.123-.08m-5.801 0c-.065.21-.1.433-.1.664 0 .414.336.75.75.75h4.5a.75.75 0 00.75-.75 2.25 2.25 0 00-.1-.664m-5.8 0A2.251 2.251 0 0113.5 2.25H15c1.012 0 1.867.668 2.15 1.586m-5.8 0c-.376.023-.75.05-1.124.08C9.095 4.01 8.25 4.973 8.25 6.108V8.25m0 0H4.875c-.621 0-1.125.504-1.125 1.125v11.25c0 .621.504 1.125 1.125 1.125h9.75c.621 0 1.125-.504 1.125-1.125V9.375c0-.621-.504-1.125-1.125-1.125H8.25zM6.75 12h.008v.008H6.75V12zm0 3h.008v.008H6.75V15zm0 3h.008v.008H6.75V18z" />
                        </svg>
                        <div className='copied-text'>{showCopyText}</div>
                    </div>

                </div>
                <button>Hash</button>
            </div>
        </form>
    </div>);
    
}

export default Blockhash;